export default () => {
    if (process.env.STORYBOOK) {
        return '/mockData';
    }

    switch (process.env.ENV) {
        case 'prod':
        case 'rel':
            return 'https://penny.londoner.tw';
        // if  proxy  IT API
        // else  proxy u 
        //        <a>  u  false
        default:
            return '/local';
    }
};

export const handlepath = () => {
    switch (process.env.ENV) {
        case 'prod':
        case 'rel':
            return 'https://www.londoner.tw';
        default:
            return '/penny-store';

    }
}
